	import mpvuePicker from '../../components/mpvue-picker/mpvuePicker.vue';
	import { getRegions, bindCard, getMyAssets } from '@/utils/api'

    export default {
        components: {
			mpvuePicker
        },
        data() {
            return {
                providerList: [],
                hasProvider: false,
                bank_branch: '',
				areaText: '',
				bank_name: '',
				realname: '',
				id_card: '',
				bank_account: '',
                positionTop: 0,
				mulLinkageTwoPicker: [],
				pickerValueDefault:[0],
				mode: '',
				deepLength: 1,
				themeColor: '#007AFF',
				pickerValueIndex: [0, 0],
				pickerText: '',
				pickerValueArray: [],
				bankArray: [{"label":"工商银行"},{"label":"农业银行"},{"label":"中国银行"},{"label":"建设银行"},{"label":"交通银行"},{"label":"招商银行"},{"label":"中信银行"},{"label":"浦发银行"},{"label":"民生银行"},{"label":"广发银行"},{"label":"兴业银行"}]
            }
        },
        methods: {
			fixIos: function(){
				setTimeout(function() {                
					var scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0;
					window.scrollTo(0, Math.max(scrollHeight - 1, 0));            
				}, 100);
			},
			showBankPicker() {
				this.$refs.bankPicker.show()
			},
			// 二级联动选择
			showMulLinkageTwoPicker() {
				this.pickerValueArray = this.mulLinkageTwoPicker
				this.mode = 'multiLinkageSelector'
				this.deepLength = 2
				this.pickerValueDefault = [0, 0]
				this.$refs.mpvuePicker.show()
			},
			onConfirm(e) {
				this.city = e.value[1];
				this.bank_region = e.label.split("-")[1];
				this.areaText = e.label.split("-").join(' ');
			},
			onCancel(e){
				
			},
			bankSelect(e) {
				this.bank_name = e.label;
			},
			async bindCard(){
				if(!this.realname.trim()){
					wx.showToast({
						icon: 'none',
						title: '请输入姓名',
						duration: 1000
					})
					return
				}
				
				if(!/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(this.id_card)){
					wx.showToast({
						icon: 'none',
						title: '请输入正确的身份证号码',
						duration: 1000
					})
					return
				}
				
				if(!this.bank_account) {
					wx.showToast({
						icon: 'none',
						title: '请输入卡号',
						duration: 1000
					})
					return
				}
				
				if(!this.bank_name) {
					wx.showToast({
						icon: 'none',
						title: '请选择开户行',
						duration: 1000
					})
					return
				}
				
				if(!this.bank_region) {
					wx.showToast({
						icon: 'none',
						title: '请选择开户地区',
						duration: 1000
					})
					return
				}
				
				if(!this.bank_branch) {
					wx.showToast({
						icon: 'none',
						title: '请输入开户行分行支行',
						duration: 1000
					})
					return
				}
				
				let params = {
					bank_branch: this.bank_branch,
					bank_name: this.bank_name, 
					realname: this.realname, 
					id_card: this.id_card, 
					bank_region: this.bank_region, 
					bank_account: this.bank_account 
				}
				
				let res = await bindCard(params)
				
				if(res.code == 200){
					wx.showToast({
						icon: 'none',
						title: '绑卡成功',
						duration: 1000
					})
					await this.getMyAssets()
					
					let curPage = this.getCurPage();
					let curParam = curPage.options || curPage.$route.query;
					let delta = curParam.delta;
					
					uni.navigateBack({
						delta: Number(delta) || 1
					})
				}
			},
			getCurPage(){
				let pages = getCurrentPages();
				let curPage = pages[pages.length-1];
				return curPage
			},
			async getMyAssets() {
				let res = await getMyAssets()
				
				if(res.code == 200){
					this.withdrawable = res.withdrawable
					this.withdrew = res.withdrew
					this.incomes = res.incomes
					uni.setStorageSync("global", {
						withdrawable: res.withdrawable,
						withdrew: res.withdrew,
						incomes: res.incomes,
						realname: res.realname,
						id_card: res.id_card,
						bank_name: res.bank_name,
						bank_account: res.bank_account
					});
				}
			}
			
        },
        async onLoad() {
			let global = uni.getStorageSync('global')
			if(global) {
				this.realname = global.realname || ''
				this.id_card = global.id_card || ''
			}
			
			let res = await getRegions()
			if(res.code == 200){
				this.mulLinkageTwoPicker = res.data
			}
        },
    }
